<template>
    <div>
        <MHeader :back="true">首页</MHeader>
        <div class="content">
          <loading v-if="loading"></loading>
          <template v-else>
            <swiper class="sw" :swiperSliders="slider"></swiper>
            <div class="container">
              <h3>热门图书</h3>
              <ul >
                <li v-for="hot in hotBooks">

                  <img :src="hot.bookCover" alt="">
                  <b>{{hot.bookName}}</b>
                </li>
              </ul>
            </div>
          </template>

      </div>

    </div>
</template>

<script>
  import Swiper from '../base/Swiper.vue';
  import MHeader from '@/base/MHeader.vue';
  import {getAll} from '../api';
  import Loading from '../base/Loading.vue';
  import axios from 'axios';

    export default {
      data() {
          return {
            slider:[],
            hotBooks:[],
            loading:true
          }

      },
       created () {
            this.getData();//获取轮播图 最热图书

      },
      methods: {
        async getData() {
          //给data其别名 对象中的属性名必须和得到的结果名字一致
          //let {data}=await getSliders();
          // 被axios.interceptors.response.use给拦截过滤去掉{}；简化后省去中间变量data

          // this.slider = await getAll();
          let [sliders,hotbooks]= await getAll();
          this.slider=sliders;
          this.hotBooks=hotbooks;
          //已经获取轮播图 最热图书
          this.loading=false;

        },

      },
        computed: {},
        components: {
          MHeader,
          Swiper,
          Loading

        }
    }
</script>

<style scoped lang="less">
  .sw{
    height: 50%;
    display: block;
  }
.container{

  width: 90%;
  height: 30px;
  margin: 0 auto;
  h3{color:#2a2a2a;}
  ul{
    display: flex;
    flex-wrap: wrap; /*默认不换行*/
    padding-bottom: 10px;
    li{
        width:33.3%;
        img{
          width: 100%;
          height: 80%;
        }

    }
  }
}

</style>



